<template lang="pug">
    v-card
        v-toolbar(flat)
            v-toolbar-title 开发规划项目管理
        v-divider
        v-combobox(
            v-model="blockname"
            :items="db_blocknames"
            label="选择区块"
        )
        v-card-actions
        v-row 
            v-col(cols="12" sm="0" lg="6")
                span 油井月报状态:{{oil_month_report_status}}
            v-col(cols="12" sm="0" lg="6")
                v-btn(color="success" dark  @click="download_oil_month_report") 下载油井月报
            v-col(cols="12" sm="0" lg="6")
                span 水井月报状态:{{water_month_report_status}}
            v-col(cols="12" sm="0" lg="6")
                v-btn(color="success" dark @click="download_water_month_report") 下载水井月报
            v-col(cols="12" sm="0" lg="6")
                span 油井日报状态:{{oil_day_report_status}}
            v-col(cols="12" sm="0" lg="6")
                v-btn(color="success" dark @click="download_oil_day_report") 下载油井日报
            v-col(cols="12" sm="0" lg="6")
                span 水井日报状态:{{water_day_report_status}}
            v-col(cols="12" sm="0" lg="6")
                v-btn(color="success" dark @click="download_water_day_report") 下载水井日报
            
            v-col(cols="12" sm="0" lg="6")
                v-btn(color="success" dark @click="update_csv_status" ) 刷新状态

            v-col(cols="12" sm="0" lg="6")
                v-btn(:disabled="!canload_group" color="success" dark @click="load_block_group_data2mem" ) 加载数据到内存中

</template>

<script>
import { get } from 'vuex-pathify'
import * as api from "@/apis/wswebapi"
export default {
    name:"PorjectManagerView",
    data: () => ({
        blockname:"",
  }),
  computed:{
      db_blocknames:get("peproject/db_blocknames"),
      oil_day_report_status:get("peproject/oil_day_report_status"),
      oil_month_report_status:get("peproject/oil_month_report_status"),
      water_day_report_status:get("peproject/water_day_report_status"),
      water_month_report_status:get("peproject/water_month_report_status"),
      canload_group(){
          if (this.oil_month_report_status && this.water_month_report_status){
              return true
          }else{
              return false 
          }
      }
  },

  methods:{
      update_csv_status(){
          this.$store.dispatch("peproject/update_blockname",this.blockname);
      },
      download_oil_day_report(){
          api.DownloadBlockData({blockname:this.blockname,collection:"oilreportday"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })
      },
      download_oil_month_report(){
          api.DownloadBlockData({blockname:this.blockname,collection:"oilreportmonth"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })
      },
      download_water_day_report(){
          api.DownloadBlockData({blockname:this.blockname,collection:"waterreportday"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })
      },
      download_water_month_report(){
          api.DownloadBlockData({blockname:this.blockname,collection:"waterreportmonth"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })
      },
    //   加载到井组数据到内存中
    load_block_group_data2mem(){
        api.LoadBlockCollection2Mem({blockname:this.blockname,collection:"oilreportmonth"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })

        api.LoadBlockCollection2Mem({blockname:this.blockname,collection:"waterreportmonth"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })
          

          api.LoadBlockGroup2Mem({blockname:this.blockname,collection:"waterreportmonth"}).then(res=>{
              if (res.code==200){
                  if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
                }
              }
          })

    }



  },
  watch:{
    // 更新区块
    blockname(){
        this.$store.dispatch("peproject/update_blockname",this.blockname)
    },
  }
}
</script>
